                "text": "De manera que abandonaron su proyecto de reconquistar el resto de sus tierras, porque tan numerosos eran los lamanitas, que les fue imposible a los nefitas sobrepujarlos; por lo que Moronah puso a todos sus ejrcitos a defender aquellas partes que l haba tomado.",
