                "verse": 23,
                "text": "Y sucedi que Nefi y Lehi fueron envueltos como por fuego, de modo que no se atrevieron a echarles mano por miedo de ser quemados. No obstante, Nefi y Lehi no se quemaban; y se hallaban como si estuviesen en medio del fuego, y no se quemaban.",
                "reference": "Helamn 5:23"
