                "verse": 13,
                "text": "Y he aqu, sus mujeres trabajaban e hilaban, y elaboraban toda clase de telas, de lino finamente tejido y ropa de toda especie para cubrir su desnudez. Y as pas en paz el ao sesenta y cuatro.",
